// components/admin/mobile-sidebar.tsx
// ————————————————————————————————————————————————
// Sidebar móvil para la sección de administración.
// Se muestra únicamente en pantallas pequeñas (< sm) y
// ofrece accesos rápidos a las vistas de administración.
//
// Estructura general:
// 1. contenedor a pantalla completa con fondo beige
// 2. botón de cierre en la parte superior derecha
// 3. navegación dividida por secciones
//
// Props
// ───────────────────────────────────────────────
// • onClose(): callback que proviene del layout padre
//   para cerrar el menú (se invoca al pulsar el botón
//   × o al hacer click en cualquier enlace).
//
// Notas
// ───────────────────────────────────────────────
// • Lucide-react se usa para los iconos.
// • Las rutas coinciden con las secciones definidas en el
//   sidebar de escritorio; si añades una nueva ruta ahí,
//   recuerda añadirla también aquí.
// • bg-[#e9d29a] = color de pared característico del
//   Kwik-E-Mart.
// ————————————————————————————————————————————————

'use client'

import React from 'react'
import Link from 'next/link'
import { PlusCircle, Eye, Megaphone, BarChart2 } from 'lucide-react'

// Tipado de las props que recibe el componente
type MobileSidebarProps = {
  /**
   * Función que cierra el sidebar móvil.
   * Normalmente la provee el layout "AdminLayout".
   */
  onClose: () => void
}

/**
 * Sidebar móvil para la interfaz de administración.
 */
export default function MobileSidebar({ onClose }: MobileSidebarProps) {
  return (
    // Contenedor a pantalla completa (posición fija)
    <div className="fixed inset-0 z-50 bg-[#e9d29a] overflow-auto">
      {/* ——— Botón de cerrar ——————————————— */}
      <div className="p-4 flex justify-end">
        <button
          onClick={onClose}
          aria-label="Cerrar menú"
          className="text-3xl font-bold focus:outline-none"
        >
          ×
        </button>
      </div>

      {/* ——— Navegación (secciones y enlaces) ——— */}
      <nav className="px-6 space-y-8">
        {/* ╭───────── Administrar datos ─────────╮ */}
        <div>
          <h2 className="text-sm font-semibold uppercase mb-2">
            Administrar datos
          </h2>

          <ul className="space-y-2">
            {/* Visualizar */}
            <li>
              <Link
                href="/admin/visualizar"
                onClick={onClose}
                className="flex items-center space-x-3 rounded-lg px-3 py-2 hover:bg-[#e0c986] transition"
              >
                <Eye size={24} />
                <span className="text-lg">Visualizar</span>
              </Link>
            </li>

            {/* Agregar */}
            <li>
              <Link
                href="/admin/agregar"
                onClick={onClose}
                className="flex items-center space-x-3 rounded-lg px-3 py-2 hover:bg-[#e0c986] transition"
              >
                <PlusCircle size={24} />
                <span className="text-lg">Agregar</span>
              </Link>
            </li>
          </ul>
        </div>

        {/* ╭────────────── Envíos ───────────────╮ */}
        <div>
          <h2 className="text-sm font-semibold uppercase mb-2">Envíos</h2>
          <ul className="space-y-2">
            <li>
              <Link
                href="/admin/difusiones"
                onClick={onClose}
                className="flex items-center space-x-3 rounded-lg px-3 py-2 hover:bg-[#e0c986] transition"
              >
                <Megaphone size={24} />
                <span className="text-lg">Difusiones</span>
              </Link>
            </li>
          </ul>
        </div>

        {/* ╭─────────── Estadísticas ────────────╮ */}
        <div>
          <h2 className="text-sm font-semibold uppercase mb-2">Estadísticas</h2>
          <ul className="space-y-2">
            <li>
              <Link
                href="/admin/estadisticas"
                onClick={onClose}
                className="flex items-center space-x-3 rounded-lg px-3 py-2 hover:bg-[#e0c986] transition"
              >
                <BarChart2 size={24} />
                <span className="text-lg">Visión general</span>
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    </div>
  )
}