// components/admin/layout.tsx
'use client';

/**
 * **AdminLayout**
 *
 * Layout raíz para todas las rutas `/admin/*`.
 *
 * ── Estructura ────────────────────────────────────────────────────────
 *  • Sidebar fijo (>= sm)          – `AdminSidebar`
 *  • Header + hamburger (xs)       – Logo + User + botón toggle
 *  • Sidebar deslizable (xs)       – `MobileSidebar`
 *  • `main` responsive             
 *
 
 */

import React, { useState, ReactNode } from 'react';
import AdminSidebar   from '@/components/admin/sidebar';
import MobileSidebar  from '@/components/admin/mobile-sidebar';
import Logo           from '@/components/navbar/logo';
import { User }       from 'lucide-react';

export default function AdminLayout({ children }: { children: ReactNode }) {
  /* -------------------------------------------------------------------- */
  /*                             Estado móvil                             */
  /* -------------------------------------------------------------------- */
  const [mobileOpen, setMobileOpen] = useState(false);
  const toggleMobile = () => setMobileOpen((o) => !o);

  /* -------------------------------------------------------------------- */
  /*                                JSX                                   */
  /* -------------------------------------------------------------------- */
  return (
    <div className="flex h-screen flex-col">
      {/* ------------------------- Sidebar desktop ----------------------- */}
      <div className="hidden sm:block">
        <AdminSidebar />
      </div>

      {/* --------------------------- Header móvil ------------------------ */}
      <header className="fixed inset-x-0 top-0 z-50 flex h-16 items-center justify-between bg-[#e9d29a] px-4 sm:hidden">
        {/* Logo + avatar fake */}
        <div className="pointer-events-none flex items-center gap-2">
          <Logo className="h-8" />
          <User size={20} />
        </div>

        {/* Botón hamburguesa / close */}
        <button
          onClick={toggleMobile}
          aria-label={mobileOpen ? 'Cerrar menú' : 'Abrir menú'}
          className="text-2xl focus:outline-none"
        >
          {mobileOpen ? '×' : '≡'}
        </button>
      </header>

      {/* ------------------------- Sidebar móvil ------------------------ */}
      {mobileOpen && <MobileSidebar onClose={toggleMobile} />}

      {/* ------------------------- Contenido ---------------------------- */}
      <main
        className="
          flex-1 overflow-auto bg-[#e9d29a] text-gray-900
          pt-[64px]              
          sm:pt-[50px]            
          sm:pl-[15rem]           
        "
      >
        {children}
      </main>
    </div>
  );
}
