// components/admin/sidebar.tsx
'use client';

/* -------------------------------------------------------------------------
 *  AdminSidebar
 *  ------------------------------------------------------------------------
 *  Sidebar “ilustrado” fijo para escritorio (≥ sm).
 *  El arte de Apu está dividido en tres capas:
 *    1.  Techo           (altura fija ROOF_H)
 *    2.  Cuerpo enlaces  (scroll interno)
 *    3.  Pies            (FEET_H)
 *  Al hacer scroll el div de enlaces crece/encoge — se calcula el corte
 *  (cutPx) restando la posición de la línea de las manos de Apu.
 * ---------------------------------------------------------------------- */

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import clsx from 'clsx';
import {
  ShoppingCart,
  BarChart2,
  Eye,
  PlusCircle,
  Tag,
} from 'lucide-react';
import { useRef, useState, useEffect } from 'react';

/* ---- Constantes visuales (tamaño / color) -------------------------------- */
const ROOF_H   = 160;              // Techo del sidebar
const FOOTER_H = 64;               // Footer global, para ajustar altura
const WALL        = '#e9d29a';
const WALL_DARK   = '#c6b282';
const SIDEBAR_W   = '15rem';
const HANDS_LINE_OFFSET = 115;     // Línea donde “cortan” las manos de Apu
const FEET_H      = 210;
const HEADROOM    = 80;            // Espacio entre techo y cartel

/* ---- Representación de la navegación ------------------------------------ */
interface NavItem {
  href:  string;
  label: string;
  icon:  React.ComponentType<React.SVGProps<SVGSVGElement>>;
}
interface NavSection {
  title: string;
  items: NavItem[];
}

const sections: NavSection[] = [
  {
    title: 'Administrar datos',
    items: [
      { href: '/admin/visualizar', label: 'Visualizar',          icon: Eye },
      { href: '/admin/agregar',    label: 'Agregar',             icon: PlusCircle },
      { href: '/admin/asignar',    label: 'Asignar promoción',   icon: Tag },
    ],
  },
  {
    title: 'Envíos',
    items: [
      { href: '/admin/difusiones', label: 'Difusiones', icon: ShoppingCart },
    ],
  },
  {
    title: 'Estadísticas',
    items: [
      { href: '/admin/estadisticas', label: 'Visión general', icon: BarChart2 },
    ],
  },
];

export default function AdminSidebar() {
  const pathname = usePathname();

  /* ---- refs para medir el corte de la ilustración --------------------- */
  const asideRef = useRef<HTMLDivElement>(null);
  const signRef  = useRef<HTMLDivElement>(null);
  const [cutPx, setCutPx] = useState(0); // px a restar al bloque scrollable

  /* Re-calcular en resize */
  useEffect(() => {
    const updateCut = () => {
      if (!asideRef.current || !signRef.current) return;
      const { top: asideTop }   = asideRef.current.getBoundingClientRect();
      const { bottom: signBot } = signRef.current.getBoundingClientRect();
      const rawCut = signBot - asideTop;
      setCutPx(Math.max(0, Math.round(rawCut - HANDS_LINE_OFFSET)));
    };
    updateCut();
    window.addEventListener('resize', updateCut);
    return () => window.removeEventListener('resize', updateCut);
  }, []);

  /* -------------------------------------------------------------------- */
  /*                                 JSX                                  */
  /* -------------------------------------------------------------------- */
  return (
    <aside
      ref={asideRef}
      className="fixed bottom-0 left-4 z-20 flex flex-col justify-end text-[#031926] font-bold"
      style={{
        top: ROOF_H,
        width: SIDEBAR_W,
        height: `calc(100vh - ${FOOTER_H}px)`,
      }}
    >
      {/* ---------- Bloque superior (techo + cartel Apu) ---------- */}
      <div
        style={{
          height: `${cutPx + HEADROOM}px`,
          backgroundColor: WALL,
          position: 'relative',
        }}
      >
        <div
          ref={signRef}
          className="absolute left-0 w-full"
          style={{ top: HEADROOM, zIndex: 10 }}
        >
          <img
            src="/images/apusidebar.png"
            alt="Apu sosteniendo el menú"
            className="block w-full pointer-events-none"
          />
        </div>
      </div>

      {/* ---------- Bloque enlaces scrollable ---------- */}
      <div
        className="overflow-y-auto"
        style={{
          height: `calc(100% - ${cutPx + HEADROOM}px - ${FEET_H}px)`,
          backgroundColor: WALL_DARK,
          boxShadow: '0 4px 12px rgba(0,0,0,0.25)',
        }}
      >
        {sections.map(({ title, items }) => {
          const expanded = items.some((it) => pathname.startsWith(it.href));
          return (
            <div key={title} className="group mt-6">
              <div className="cursor-pointer px-6 pb-2 text-xs uppercase text-gray-900">
                {title}
              </div>

              <div
                className={clsx(
                  'overflow-hidden transition-all duration-500 ease-out',
                  expanded ? 'max-h-screen opacity-100' : 'max-h-0 opacity-0',
                  'group-hover:max-h-32 group-hover:opacity-100',
                )}
              >
                {items.map(({ href, label, icon: Icon }) => {
                  const active = pathname.startsWith(href);
                  return (
                    <Link
                      key={href}
                      href={href}
                      className={clsx(
                        'flex items-center gap-3 px-6 py-3 rounded-r-md transition',
                        active ? 'brightness-110' : 'hover:bg-opacity-10',
                      )}
                      style={{
                        backgroundColor: active ? WALL : 'transparent',
                      }}
                    >
                      <Icon width={20} height={20} strokeWidth={2} />
                      <span className="text-sm">{label}</span>
                    </Link>
                  );
                })}
              </div>
            </div>
          );
        })}
      </div>

      {/* ---------- Bloque pies de Apu ---------- */}
      <div style={{ height: FEET_H, overflow: 'hidden' }}>
        <img
          src="/images/apufoot.png"
          alt="Pies de Apu"
          className="w-full object-cover pointer-events-none"
          style={{ transform: 'scaleX(1.7)', transformOrigin: 'top center' }}
        />
      </div>
    </aside>
  );
}
